
import React, { useEffect, useRef, useState, useContext } from 'react';
import { Space, Table, Tag, Modal, Switch } from 'antd';
import { billCount, billPage } from '@/api/AdminDashboard'
import dayjs from 'dayjs'
import AdminFiled from '@/Shared/AdminFiled/AdminFiled';
import AdminSupportAgentTableAction from '@/Components/PubTable/AdminSupportAgentTableAction';
import { Elderlies } from "@/mock/admin/data";
import ls from 'store2'
import AdminSupportAgentTable from '@/Components/PubTable/AdminSupportAgentTable';
import { SidebarContext } from '@/Context/CustomContext';


const AdminDashboard = () => {

    const { role } = useContext(SidebarContext)
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [activeTab, setActiveTab] = useState('This month')
    const [menus, setMenus] = useState([
        {
            text: "This month",
            bo: true,
            warring: false
        },
        {
            text: "All the time",
            bo: false,
            warring: false
        },

    ])
    const [query, setQuery] = useState({
        "size": 10,
        "current": 1
    })
    const [totle, setTotle] = useState(0)
    const [data, setData] = useState([])
    const [statistics, SetStatistics] = useState({
        paidBillTotal: 0,
        unpaidBillTotal: 0,


    })


    let columns = [
        {
            title: 'Distributor',
            key: "id",

            render: (row) => (
                <button >
                    <AdminFiled data={row} />
                </button>
            )
        },
        {
            title: 'Contact Person',

            render: (row) => <button>
                <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.contactPerson}</span>
            </button>,
        },
        {
            title: 'Business Address',

            render: (row) => <button>
                <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.businessAdress}</span>
            </button>,
        },
        {
            title: 'Contact',

            render: (row) => <button>
                <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.contactNumber}</span>
            </button>,
        },
        {
            title: 'Bill',
            key: "id",
            render: (row) => (
                <>
                    {row.status == 0 ? <span className='text-onBackWarring'>-{row.billTotal}</span> : <span className='text-green'>{row.billTotal}</span>

                    }
                </>

            )
        },
        {
            title: 'Status',
            key: "id",
            render: (row) => (
                <>
                    {
                        row.status == 0 ? <span className='text-onBackWarring'>Unpaid</span> : <span className='text-green'>Paid</span>

                    }
                </>
            )
        },
        {
            title: 'payTime',
            key: "id",
            render: (row) => (
                <span className='text-onBackWarring'>{row.payTime}</span>
            )
        },
        {
            title: 'Invoice',

            render: (row) => <div className='flex items-center '>
                {
                    row.invoiceUrl && <>
                        <div>

                            <svg xmlns="http://www.w3.org/2000/svg" width="15" height="11" viewBox="0 0 15 11" fill="none">
                                <path d="M7.32723 10.1819C2.82892 10.1819 0.177932 5.92399 0.0671119 5.74311C0.00345881 5.63824 -0.0134848 5.51918 0.0103279 5.40836C-0.0139428 5.29571 0.00391675 5.17298 0.0707754 5.06765C0.185259 4.88631 2.93104 0.621094 7.32677 0.621094C11.8251 0.621094 14.4761 4.87898 14.5869 5.05987C14.6505 5.16474 14.6675 5.2838 14.6437 5.39462C14.6679 5.50727 14.6501 5.63 14.5832 5.73532C14.4687 5.91713 11.723 10.1819 7.32723 10.1819ZM0.944518 5.41065C1.43588 6.12869 3.78647 9.26647 7.32723 9.26647C10.7943 9.26647 13.203 6.11587 13.7099 5.39279C13.2186 4.67474 10.868 1.53697 7.32723 1.53697C3.8602 1.53697 1.45145 4.68711 0.944518 5.41065ZM7.32723 8.01218C5.87283 8.01218 4.68952 6.82888 4.68952 5.37447C4.68952 3.92007 5.87283 2.73676 7.32723 2.73676C8.78164 2.73676 9.96494 3.92007 9.96494 5.37447C9.96494 6.82888 8.78164 8.01218 7.32723 8.01218ZM7.32723 3.65309C6.37793 3.65309 5.60539 4.42563 5.60539 5.37493C5.60539 6.32423 6.37793 7.09677 7.32723 7.09677C8.27653 7.09677 9.04907 6.32423 9.04907 5.37493C9.04907 4.42563 8.27653 3.65309 7.32723 3.65309Z" fill="#333951" />
                            </svg>
                        </div>
                        <div className='ml-[10px]'>
                            <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 14 14" fill="none">
                                <path d="M14 13.125C14 13.6082 13.6082 14 13.125 14H0.875C0.391754 14 0 13.6082 0 13.125C0 12.6418 0.391754 12.25 0.875 12.25H13.125C13.6082 12.25 14 12.6418 14 13.125ZM6.38129 10.3531C6.55216 10.524 6.77605 10.6094 7 10.6094C7.22389 10.6094 7.44789 10.524 7.61871 10.3531L10.7189 7.25296C11.0606 6.91124 11.0606 6.35723 10.7189 6.01552C10.3772 5.6738 9.82316 5.6738 9.48145 6.01552L7.875 7.62196V0.875C7.875 0.391754 7.48325 0 7 0C6.51675 0 6.125 0.391754 6.125 0.875V7.62196L4.51855 6.01552C4.17684 5.6738 3.62283 5.6738 3.28111 6.01552C2.9394 6.35723 2.9394 6.91124 3.28111 7.25296L6.38129 10.3531Z" fill="black" />
                            </svg>
                        </div>
                    </>
                }

            </div>,
        },

        // {
        //     title: "",
        //     key: "id",
        //     width: 100,
        //     render: (row) => (
        //         // <CustomButton  >Pay now</CustomButton>
        //         <AdminSupportAgentTableAction />
        //     )
        // }
    ]
    const handlePaginationChange = (page, pageSize) => {
        setQuery({
            current: page,
            size: pageSize
        })

    };
    const paginationOptions = {
        onChange: handlePaginationChange,
        current: query.current,
        pageSize: 10,
        total: totle
    };

    const HandleTabText = (e) => {
        setActiveTab(e)
        setIsModalOpen(true)
    }
    const getlist = (params) => {

        billCount({ ...params }).then(res => {
            console.log("res", res)
            SetStatistics({ ...res.data })
        })

        billPage({ ...params, ...query }).then(res => {
            console.log("res", res)
            setData(res.data.records)
            setTotle(res.data.total)

        })
    }
    useEffect(() => {
        let cquery = {}
        const now = dayjs();
        if (activeTab == "This month") {
            cquery = { startCreatedTime: now.startOf('month').format('YYYY-MM-DD HH:mm:ss'), endCreatedTime: now.endOf('month').format('YYYY-MM-DD HH:mm:ss') }
        }
        getlist(cquery)
    }, [activeTab, query.current])

    return (
        <div className='pl-[20px] pr-[50px]'>
            <div className='h-[100px] flex items-center justify-between'>
                <div className='flex items-center'>

                    <div className=' text-[28px] font-semibold font-cblock ' >Dashboard</div>

                </div>

            </div>
            <div className=' pb-[10px] flex'>
                {
                    menus.map((e, key) => {
                        return (
                            <div
                                onClick={() => HandleTabText(e.text)}
                                className={` relative re cursor-pointer h-[40px] mr-[10px] pl-[20px] pr-[20px]  rounded-[20px]   flex items-center ${activeTab == e.text ? "text-white bg-cblock " : "text-OnButtonNormal bg-white  "} `}>
                                {e.text}
                                {e.warring && <div className={`w-[8px] h-[8px] rounded-[100px] bg-onBackWarring  absolute  right-[5px]`} > </div>}
                            </div>
                        )
                    })
                }
            </div>
            <div className='pt-[29px] flex '>

                <div style={{ backgroundColor: '#F7F7FF' }}
                    className=' mr-[10px] w-[200px] pb-[40px]  flex flex-col items-center rounded-t-[10px]' >
                    <div className='mt-[18px]'>
                        <div className=' w-[32px] h-[32px] bg-green rounded-full  justify-center items-center flex' >
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none">
                                <path d="M8 16C5.86312 16 3.85413 15.1679 2.34313 13.6569C0.832125 12.1459 0 10.1369 0 8C0 5.86312 0.832156 3.85416 2.34313 2.34313C3.85409 0.832094 5.86312 0 8 0C10.1369 0 12.1459 0.832125 13.6569 2.34313C15.1679 3.85413 16 5.86312 16 8C16 10.1369 15.1678 12.1458 13.6569 13.6569C12.1459 15.1679 10.1369 16 8 16ZM8 1C4.14019 1 1 4.14019 1 8C1 11.8598 4.14019 15 8 15C11.8598 15 15 11.8598 15 8C15 4.14019 11.8598 1 8 1Z" fill="white" />
                                <path d="M8 7.5C7.31075 7.5 6.75 6.93925 6.75 6.25C6.75 5.56075 7.31075 5 8 5C8.68925 5 9.25 5.56075 9.25 6.25C9.25 6.52612 9.47384 6.75 9.75 6.75C10.0262 6.75 10.25 6.52612 10.25 6.25C10.25 5.18122 9.50069 4.28475 8.5 4.05669V3.5C8.5 3.22387 8.27616 3 8 3C7.72384 3 7.5 3.22387 7.5 3.5V4.05669C6.49931 4.28475 5.75 5.18122 5.75 6.25C5.75 7.49066 6.75934 8.5 8 8.5C8.68925 8.5 9.25 9.06075 9.25 9.75C9.25 10.4392 8.68925 11 8 11C7.31075 11 6.75 10.4392 6.75 9.75C6.75 9.47388 6.52616 9.25 6.25 9.25C5.97384 9.25 5.75 9.47388 5.75 9.75C5.75 10.8188 6.49931 11.7153 7.5 11.9433V12.5C7.5 12.7761 7.72384 13 8 13C8.27616 13 8.5 12.7761 8.5 12.5V11.9433C9.50069 11.7153 10.25 10.8188 10.25 9.75C10.25 8.50934 9.24066 7.5 8 7.5Z" fill="white" />
                            </svg>
                        </div>
                    </div>
                    <div className='mt-[11px] text-cblock  text-[14px]   '>Total </div>
                    <div className='mt-[26px] text-cblock  text-[40px] font-bold     '> ${statistics.paidBillTotal}</div>
                </div>
                <div style={{ backgroundColor: '#F7F7FF' }}
                    className=' mr-[10px] w-[200px] pb-[40px]  flex flex-col items-center rounded-t-[10px] bg-onBackWarring' >
                    <div className='mt-[18px]'>
                        <div className=' w-[32px] h-[32px] bg-green rounded-full  justify-center items-center flex bg-onBackWarring' >
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none">
                                <path d="M8 16C5.86312 16 3.85413 15.1679 2.34313 13.6569C0.832125 12.1459 0 10.1369 0 8C0 5.86312 0.832156 3.85416 2.34313 2.34313C3.85409 0.832094 5.86312 0 8 0C10.1369 0 12.1459 0.832125 13.6569 2.34313C15.1679 3.85413 16 5.86312 16 8C16 10.1369 15.1678 12.1458 13.6569 13.6569C12.1459 15.1679 10.1369 16 8 16ZM8 1C4.14019 1 1 4.14019 1 8C1 11.8598 4.14019 15 8 15C11.8598 15 15 11.8598 15 8C15 4.14019 11.8598 1 8 1Z" fill="white" />
                                <path d="M8 7.5C7.31075 7.5 6.75 6.93925 6.75 6.25C6.75 5.56075 7.31075 5 8 5C8.68925 5 9.25 5.56075 9.25 6.25C9.25 6.52612 9.47384 6.75 9.75 6.75C10.0262 6.75 10.25 6.52612 10.25 6.25C10.25 5.18122 9.50069 4.28475 8.5 4.05669V3.5C8.5 3.22387 8.27616 3 8 3C7.72384 3 7.5 3.22387 7.5 3.5V4.05669C6.49931 4.28475 5.75 5.18122 5.75 6.25C5.75 7.49066 6.75934 8.5 8 8.5C8.68925 8.5 9.25 9.06075 9.25 9.75C9.25 10.4392 8.68925 11 8 11C7.31075 11 6.75 10.4392 6.75 9.75C6.75 9.47388 6.52616 9.25 6.25 9.25C5.97384 9.25 5.75 9.47388 5.75 9.75C5.75 10.8188 6.49931 11.7153 7.5 11.9433V12.5C7.5 12.7761 7.72384 13 8 13C8.27616 13 8.5 12.7761 8.5 12.5V11.9433C9.50069 11.7153 10.25 10.8188 10.25 9.75C10.25 8.50934 9.24066 7.5 8 7.5Z" fill="white" />
                            </svg>
                        </div>
                    </div>
                    <div className='mt-[11px] text-cblock  text-[14px]   '>Unpaid</div>
                    <div className='mt-[26px] text-cblock  text-[40px] font-bold     '> ${statistics.unpaidBillTotal}</div>
                </div>

            </div>
            <div className='mt-[38px] '>
                <div className='font-semibold text-[20px] font-cblock  pb-[10px]'>Billing</div>
                <Table pagination={paginationOptions} columns={columns} dataSource={data} />

            </div>



        </div>
    )
}

export default AdminDashboard;